@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif
}

body {
    margin-top: 30px;
    background:rgb(3,22,41);
    background: radial-gradient(circle, rgba(3,22,41,1) 8%, rgba(2,6,48,1) 100%);
}
.multiline-ellipsis {
    display: -webkit-box;       /* Required for the multiline effect */
    -webkit-line-clamp: 2;      /* Number of lines to display */
    -webkit-box-orient: vertical;
    overflow: hidden;           /* Hide overflowing content */
    text-overflow: ellipsis;    /* Add ellipsis (...) */
  }
  .multiline-ellipsis-3 {
    display: -webkit-box;       /* Required for the multiline effect */
    -webkit-line-clamp: 3;      /* Number of lines to display */
    -webkit-box-orient: vertical;
    overflow: hidden;           /* Hide overflowing content */
    text-overflow: ellipsis;    /* Add ellipsis (...) */
  }
.container {
    min-height: 100vh;
    padding: 20px 0;
    display: flex;
    justify-content: space-evenly;
    width:100%;
}

p {
    margin: 0px
}

.card {
    width: 340px;
    height: 500px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    background: #fff;
    transition: all 0.5s ease;
    cursor: pointer;
    user-select: none;
    z-index: 10;
    overflow: hidden;
    margin-top:30px;
}

.card .backgroundEffect {
    bottom: 0;
    height: 0px;
    width: 100%
}

.card:hover {
    color: #fff;
    transform: scale(1.025);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 5px 10px
}

.card:hover .backgroundEffect {
    bottom: 0;
    height: 320px;
    width: 100%;
    position: absolute;
    z-index: -1;
    background: #1b9ce3;
    animation: popBackground 0.3s ease-in
}

@keyframes popBackground {
    0% {
        height: 20px;
        border-top-left-radius: 50%;
        border-top-right-radius: 50%
    }

    50% {
        height: 80px;
        border-top-left-radius: 75%;
        border-top-right-radius: 75%
    }

    75% {
        height: 160px;
        border-top-left-radius: 85%;
        border-top-right-radius: 85%
    }

    100% {
        height: 320px;
        border-top-left-radius: 100%;
        border-top-right-radius: 100%
    }
}

.card .pic {
    position: relative
}

.card .pic img {
    width: 100%;
    height: 280px;
    object-fit: cover
}

.card .date {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 70px;
    background-color: #1b9ce3;
    color: white;
    position: absolute;
    bottom: 0px;
    transition: all ease
}

.card .date .day {
    font-size: 14px;
    font-weight: 600
}

.card .date .month,
.card .date .year {
    font-size: 10px
}

.card .text-muted {
    font-size: 12px
}

.card:hover .text-muted {
    color: #fff !important
}

.card .content {
    padding: 0 20px
}

.card .content .btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 10px;
    background-color: #1b9ce3;
    border-radius: 25px;
    font-size: 12px;
    border: none
}

.card:hover .content .btn {
    background: #fff;
    color: #1b9ce3;
    box-shadow: #0000001a 0px 3px 5px
}

.card .content .btn .fas {
    font-size: 10px;
    padding-left: 5px
}

.card .content .foot .admin {
    color: #1b9ce3;
    font-size: 12px
}

.card:hover .content .foot .admin {
    color: #fff
}

.card .content .foot .icon {
    font-size: 12px
}

.d-lg-flex {
    display: flex !important;
    justify-content: space-around;
    width: 90%;
    flex-wrap: wrap;
}


@media (min-width: 1201px) {
    .container, .container-lg, .container-md, .container-sm, .container-xl {
        max-width: 1300px;
    }
}
@media(max-width:1200px){
    .d-lg-flex {
        width: 100%;}
}